function sliderReviews() {
    var swiper = new Swiper('.reviews .swiper', {
        slidesPerView: '1',
        spaceBetween: 20,
        centeredSlides: true,
    })
}


$(document).ready(function() {
    sliderReviews();


    //Accordion
    $(".item-show").click((function() {
        $(this).toggleClass("item-show--active")
        $(this).siblings(".item-hidden").slideToggle()
    }));

})

